<!-- 留言板页面组件 -->
<template>
	<view>
		<view class="search-input">
			<u--input placeholder="搜索留言内容" prefixIcon="search"
				prefixIconStyle="font-size: 50rpx;color: #909399"
				v-model="query.messageContent"
				@input="$u.debounce(searchList, 500)"></u--input>
		</view>
		<view v-if="dataList.length || status === 'loading'" style="margin-top: 100rpx;">
			<u-list @scrolltolower="scrolltolower" height="calc(100vh - 280rpx)">
				<u-list-item v-for="(item, index) in dataList" :key="item.id">
					<Record @reply="reply" :itemData="item" />
				</u-list-item>
				<u-loadmore :status="status" />
			</u-list>
			<u-popup :show="showCommentPopup">
				<view style="padding: 24rpx;box-sizing: border-box;display: flex;flex-direction: column;gap: 24rpx;">
					<view class="header-box flexb">
						<view class="flexs" style="gap: 8rpx;">
							<u-avatar src="" shape="square"></u-avatar>
							<u-text :text="name"></u-text>
						</view>
						<text style="color: #3c9cff;" @click="closeCommentPopup">取消</text>
					</view>
					<u--textarea v-model="commentVal" placeholder="请输入内容" :adjustPosition="false"
					:maxlength="-1" border="none" adjustPosition cursorSpacing="230"></u--textarea>
					<view class="bottom-box flexe">
						<u-button customStyle="width: auto; margin: 0" text="留言" type="primary" :disabled="!commentVal"
						@tap="publish"></u-button>
					</view>
				</view>
			</u-popup>
		</view>
		<u-empty v-else mode="list" marginTop="40%" icon="http://cdn.uviewui.com/uview/empty/list.png" />
		<view class="bottom-btn safe-bottom">
			<u-button type="primary" text="留 言" @click="comment"></u-button>
		</view>
		
	</view>
</template>

<script>
	import Record from './record.vue'
	import {
		createUUID
	} from '@/utils/common.js'
	
	// 用户信息
	const {
		id,
		userType,
		name,
		phone,
		identity
	} = uni.getStorageSync('user_info');
	
	export default {
		components: {
			Record
		},
		data() {
			return {
				query: {
					page: 1,
					rows: 30,
					messageContent: '',
				},
				status: 'loading',
				dataList: [],
				// 打开留言输入框
				showCommentPopup: false,
				// 评论内容
				commentVal: '',
				// 留言信息
				activeMessage: null,
				// 用户名
				name
			}
		},
		created() {
			this.loadmore()
		},
		methods: {
			searchList() {
				this.query.page = 1
				this.dataList = []
				this.loadmore()
			},
			scrolltolower() {
				this.loadmore()
			},
			async loadmore() {
				this.status = 'loading'
				const {
					rows, records
				} = await uni.$u.http.post(`/messageBoard/selectListByPage`, null, {
					params: this.query,
					custom: {
						loading: false
					}
				})
				if (!rows) return
				if (rows.length === 0 || rows.length < this.query.rows) {
					if(records > this.dataList.length) this.dataList.push(...rows)
					return this.status = 'nomore'
				}
				if (rows.length >= this.query.rows) {
					this.dataList.push(...rows)
					this.query.page++
					this.status = 'loadmore'
				}
			},
			// 关闭评论输入框
			closeCommentPopup() {
				this.showCommentPopup = false
				this.activeMessage = null
				this.commentVal = ''
			},
			// 打开回复
			reply(data) {
				this.showCommentPopup = true
				this.activeMessage = data
			},
			handleReply() {
				this.dataList.forEach(i => {
					if (i.uuid === this.activeMessage.uuid) {
						i.children = [{
							uuid: createUUID(),
							url: 'https://cdn.uviewui.com/uview/album/7.jpg',
							name: '大漠苍狼new',
							time: '1小时前',
							message: this.commentVal
						}, ...i.children]
					}
				})
				// this.searchList()
			},
			// 发布
			publish(data) {
				if(this.activeMessage) this.handleReply()
				else this.handleComment()
			},
			// 添加留言
			comment() {
				console.log(uni.getStorageSync('user_info'));
				this.showCommentPopup = true
			},
			async handleComment() {
				await uni.$u.http.post(`/messageBoard/add`, null, {
					params: {
						unitName: identity,
						liaisonName: name,
						liaisonTel: phone,
						messageContent: this.commentVal,
						remark: ''
					}
				})
				uni.showToast({
					title: '留言成功',
					duration: 2000
				});
				this.searchList()
				this.closeCommentPopup()
			}
		},
	}
</script>

<style lang="scss">
	.u-page {
		padding-bottom: 0;
		padding-bottom: constant(safe-area-inset-bottom);
		padding-bottom: env(safe-area-inset-bottom);
	}
	
	.search-input {
		box-sizing: border-box;
		width: 96vw;
		left: 2vw;
	}
	
	.bottom-btn {
		padding: 0px 24rpx;
		position: absolute;
		bottom: 24rpx;
		bottom: constant(safe-area-inset-bottom);
		bottom: env(safe-area-inset-bottom);
		width: 100%;
		box-sizing: border-box;
	}
	
	.header-box {}
	
	.bottom-box {
		
	}
</style>